<template>
  <router-link  class="etiqueta"
  :class="ocupado ? 'ocup' : 'desocup'"
  :to="{name:'rifa-panel', params:{id:cliente}}"
  >
    <h3>{{numero}}</h3>
    <p v-if="ocupado">Ocupado</p>
    <p v-else>Desocupado</p>
  </router-link >

</template>

<script>
export default {

    props:{
        numero:{
            type:String,
            default: ''
        },
        ocupado:{
            type:Boolean,

        },
        cliente:{
            type: String,
            default: '6456464'
        }
    }

}
</script>

<style scoped>

    .etiqueta{
        color: white;
        padding-top: 10px;
        height: 100px;
        width: 150px;
        border: solid;
    }

    .ocup{
        background-color: red;
    }

    .desocup{
        background-color: green;
    }

</style>